<script setup>
import { onMounted, ref, onUnmounted } from 'vue';

const isMiniMobile = ref(window.innerWidth <= 435);

const onResize = () => {
	isMiniMobile.value = window.innerWidth <= 435;
};

onMounted(() => {
	const list = document.querySelector('.main_con04_logos');
